import { _decorator, Component, Node,AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('DhAudio')
export class DhAudio extends Component {
    @property([AudioClip])
    public bgmAudio = [];
    private urlArr:string[] = ["sound/bgm_mg_loop","sound/bgm_fs_loop","sound/bgm_bigwin"];
    private url: string = "audio/bgm_mg";
    private bigwinurl: string = "audio/bgm_bonus";
    private roll: string = "audio/GeneralAudio/spinBtn"
    private click: string = "audio/Click"
    private ReelStopNormal: string = "audio/GeneralAudio/spinReelStopNormal"
    private NormalWinBgm: string = "audio/GeneralAudio/prizeMgSmallwin"
    private startbw: string = "audio/GeneralAudio/mgCurtainTrans"
    private cpjump: string = "audio/GeneralAudio/voxJumpNormal"
    private Totalmain: string = "audio/BonusAudio/bgmTotalMain"
    private TotalEnd: string = "audio/BonusAudio/bgmTotalEnd"
    private laughMalevoice: string = "audio/GeneralAudio/voxLoveMale"
    private laughFemalevoice: string = "audio/GeneralAudio/voxLoveFemale"
    private push: string = "audio/GeneralAudio/voxTeasePush"
    private shy: string = "audio/GeneralAudio/voxTeaseShy"
    private mgsymscatter: string = "audio/GeneralAudio/mgSymScatter"
    private bigwmain: string = "audio/GeneralAudio/bgmBigwinMain"
    private bigwend: string ="audio/GeneralAudio/bgmBigwinEnd"
    private bigwendfire: string ="audio/GeneralAudio/bgmBigwinEndFirework"
    private playfakemusic: string = "audio/GeneralAudio/mgFastspin"
    start() {
        this.playBgm(1);
    }

    playBgm (index:number) {
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    stopall(){
        tgx.AudioMgr.inst.audio.stopAll();
    }



    normalToBigwin(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.bigwinurl, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    bigwinToNormal(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public playclick()
    {
        tgx.AudioMgr.inst.audio.playEffect(this.click, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public bigwinmain(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playEffect(this.bigwmain, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public async bigwinend(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playEffect(this.bigwend, ModuleDef.SLOTS_DOUBLE_HAPPY);
        tgx.AudioMgr.inst.audio.playEffect(this.bigwendfire, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public playfake(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playEffect(this.playfakemusic, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public playroll()
    {
        tgx.AudioMgr.inst.audio.playEffect(this.roll, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public ReelStop()
    {
        tgx.AudioMgr.inst.audio.playEffect(this.ReelStopNormal, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public laughMale(){
        tgx.AudioMgr.inst.audio.playEffect(this.laughMalevoice, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public voicePush(){
        tgx.AudioMgr.inst.audio.playEffect(this.push, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public voiceShy(){
        tgx.AudioMgr.inst.audio.playEffect(this.shy, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public mgscatter(){
        tgx.AudioMgr.inst.audio.playEffect(this.mgsymscatter, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public laughFemale(){
        tgx.AudioMgr.inst.audio.playEffect(this.laughFemalevoice, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public NormalWin()
    {
        tgx.AudioMgr.inst.audio.playEffect(this.NormalWinBgm, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public startbigwin()
    {
        tgx.AudioMgr.inst.audio.playEffect(this.startbw, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }
    
    public mgjump()
    {
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playEffect(this.cpjump, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    public async bigWinEnd(){
        tgx.AudioMgr.inst.audio.stopAll();
        tgx.AudioMgr.inst.audio.playEffect(this.Totalmain, ModuleDef.SLOTS_DOUBLE_HAPPY);
        await new Promise(resolve => setTimeout(resolve, 3000));
        tgx.AudioMgr.inst.audio.playEffect(this.TotalEnd, ModuleDef.SLOTS_DOUBLE_HAPPY);
    }

    update(deltaTime: number) {
        
    }
}


