import { instantiate } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { dfMain } from './dfMain';
import { df } from './cDf';
const { ccclass, property } = _decorator;

@ccclass('DhDetail')
export class DhDetail extends Component {

    private m_mainTs: dfMain;

    start() {
        this.m_mainTs = df.node;
    }

    return(){
        for(let i = 0;i<15 ; i++){
            if(df.node.detailshow.children[0]){
                df.node.detailshow.children[0].destroy();
                df.node.detailflag = false;
            }
            
        }
    } 
    
    update(deltaTime: number) {
        
    }
}


