    recycleResult(result:Node) {
                for(let i = 0;i<=2;i++){
                    this.speacil.children[i].active = false;
                }
                // 建立索引映射表，和 playRealResult 中一致
                const symbolIndexMap: { [key: string]: number } = {
                    '001': 0,
                    '002': 1,
                    '003': 2,
                    '004': 3,
                    '005': 4,
                    '006': 5,
                    '007': 6,
                    '008': 7,
                    '009': 8,
                    '010': 9,
                    '011': 10,
                    '012': 11,
                    '013': 12,
                    '014': 13,
                    '015': 14,
                    '016': 15,
                    '017': 16,
                    '018': 17,
                    '019': 18
                };
                // 遍历每一列
                for (let i = 0; i < result.children.length; i++) {
                    const column = result.children[i];
                    const symbols = column.children.slice(); // 拷贝一下，避免迭代中修改数组
                    for (let j = 0; j < symbols.length; j++) {
                        const symbolNode = symbols[j];
                        symbolNode.getComponent(UIOpacity)!.opacity = 255;
                        const name = symbolNode.name;
                        const index = symbolIndexMap[name] ?? -1;
                        if (index !== -1) {
                            df.node.objectPool.put(index, symbolNode); // 放回对象池
                        } else {
                            console.warn(`未识别的符号名: ${name}, 无法回收`);
                            symbolNode.destroy(); // 或者销毁它，防止泄露
                        }
                    }
                }
    }
    
    playLineEffect(payline: string,paylineNum :number,effectline:any,lightnode:any){
        const index = parseInt(payline) - 1;
        payline = index.toString();
        if(lightnode.length<paylineNum){
            lightnode.push(payline);
        }    
        effectline.children[payline].active = true;
    }
    
    closeLineEffect(lightnode:any,effectline:any){
        while(lightnode.length>0){//如果上次中奖会存储中奖节点进lightnode，此时可以用其关闭中奖线
            effectline.children[lightnode[0]].active = false ;
            lightnode.shift();
        }
    }

