/**
 * 掉落花朵效果：随机生成一朵花，从屏幕上方随机向左或向右飘落
 */
      dropFlower() {
    // 随机生成花朵的初始 x 坐标
    let rx = Math.floor(Math.random() * 401 - 200);
    // 随机生成花朵的初始 y 坐标（范围：720 ~ 820）
    let ry = 770 + Math.floor(Math.random() * 100 - 50);
    
    // 随机决定飘落方向（true=向右，false=向左）
    let isRight = Math.random() > 0.5;
    // 根据方向设置目标 x 坐标
    let tx = isRight ? 410 : -410; // 向右410，向左-410
    // 随机生成花朵的目标 y 坐标（范围：-300 ~ 300）
    let ty = Math.floor(Math.random() * 600 - 300);
    
    // 随机生成花朵的缩放比例（5/12 ~ 7/12）
    let rScale = (Math.floor(Math.random() * 3 + 5)) / 12;
    // 随机生成花朵的初始旋转角度（0° ~ 359°）
    let rRotation = Math.floor(Math.random() * 360);
    // 随机生成花朵的目标旋转角度（初始角度 + 随机增量）
    let targetRotation = rRotation + Math.floor(Math.random() * 360) + 180; // 至少旋转180度
    
    // 实例化花朵节点
    let newNode: Node = instantiate(this.flower);
    // 设置花朵的初始位置、缩放和旋转
    newNode.position = v3(rx, ry);
    newNode.scale = v3(rScale, rScale);
    newNode.angle = rRotation;
    
    // 将花朵添加到容器中
    this.flowercontainer.addChild(newNode);
    
    // 设置花朵的目标位置
    let aimPos = v3(tx, ty);
    
    // 使用缓动动画让花朵在 2 秒内移动到目标位置并旋转，然后销毁
    tween(newNode)
        .to(3, { 
            position: aimPos,
            angle: targetRotation  // 同时旋转到目标角度
        })
        .call(() => { newNode.destroy(); })  // 动画完成后销毁花朵
        .start();
    }

