import { tween } from "cc";
import { Component, _decorator } from "cc";


const { ccclass, property } = _decorator;

@ccclass('lampUI')
export class lampUI extends Component {
    private idx: number = 0;
    private hideArr: number[] = [];
    onLoad() {
        this.idx = 0;
        this.hideArr = []
    }

    init() {
        this.unschedule(this.showLine);
        for (var i in this.node.children) {
            this.node.children[i].opacity = 0;
        }
        this.scheduleOnce(this.showLine, 1)
    }

    start() {
        this.init();
    }

    setHideMessage(arr: number[]) {
        this.hideArr = arr;
        this.init();
    }

    showLine() {
        if (this.hideArr.length >= this.node.children.length) {
            return;
        }
        if (this.idx >= this.node.children.length) {
            this.idx = 0;
        }
        if (this.hideArr.indexOf(this.idx) != -1) {
            this.idx++;
            this.showLine()
            return;
        }
        for (var i in this.node.children) {
            if (i == this.idx + "") {
                var node = this.node.children[i]
                node.opacity = 255
                var w = node.w
                if (w < (this.node.w - 30)) {
                    node.x = (this.node.w - w) / 2
                    tween(node)
                        .delay(5)
                        .call(() => {
                            this.idx++;
                            this.showLine();
                        })
                        .start()
                } else {
                    node.x = 30;
                    tween(node)
                        .delay(3)
                        .to(8, { x: -w })
                        .call(() => {
                            this.idx++;
                            this.showLine();
                        })
                        .start()
                }
            } else {
                this.node.children[i].opacity = 0;
            }
        }
    }
}
